const mutations = {
  MUTATE_CURRENT_PEOPLE(state, payload) {
    state.currentPeople = payload;
  },
  MUTATE_MEMBERS(state, payload) {
    state.membersList = payload;
  },
  MUTATE_FIGURE(state, payload) {
    state.figure = payload;
  },
};

const actions = {
  // 
  async GET_CURRENT_MEMBER({ dispatch, commit }, params) {
    try {
      commit("MUTATE_CURRENT_PEOPLE", { id: 1 });

      var data = [];

      const currentUser = await db.collection("currentUser").get();
      currentUser.forEach((collection) => {
        data.push({ id: collection.id, ...collection.data() });
      });
      commit("MUTATE_CURRENT_PEOPLE", data[0]);
    } catch (error) {
      return await dispatch("SHOW_ERROR", error, { root: true });
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
};
